import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Typography } from '@mui/material';

// routes
import { PATH_ADMIN } from '../../routes/paths';
import { FormProvider, RHFSelect, RHFSwitch, RHFTextField } from '../../components/hook-form';

import axios from '../../utils/axios';
// ----------------------------------------------------------------------

Habukhannewresult.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object,
};
export default function Habukhannewresult({ isEdit, currentUser }) {
  const { enqueueSnackbar } = useSnackbar();
  const Habukhanexam = ['WAEC', 'NECO', 'NABTEB']
  const NewUserSchema = Yup.object().shape({
    exam_name: Yup.string().required('Exam Name Required'),
    pin: Yup.string().required('Pin Required'),
    serial: Yup.string().required('Serial Number  Required'),
  });
  const defaultValues = useMemo(
    () => ({
      exam_name: currentUser?.exam_name || '',
      serial: currentUser?.exam_serial || '',
      pin: currentUser?.exam_pin || '',
      plan_status: currentUser?.plan_status || false,
      plan_id: currentUser?.plan_id || false
    }),
    [currentUser]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  useEffect(() => {
    if (isEdit && currentUser !== undefined) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);
  const values = watch();
  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/add/result/plan/new/habukhan/safe/url/${AccessToken}/secure`, data);
      enqueueSnackbar('Create success!');
      navigate(PATH_ADMIN.plan.exam);
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }
  const onCreateReset = async (data) => {
    try {
      await axios.post(`/api/add/result/plan/new/habukhan/safe/url/${AccessToken}/secure`, data);
      reset();
      enqueueSnackbar('Create success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  const onEdit = async (data) => {
    try {
      await axios.post(`/api/system/admin/edit/resultplan/resultplan/habukhan/secure/boss/asd/asd/changes/${AccessToken}/secure`, data)
      navigate(PATH_ADMIN.plan.exam);
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  const onEditReset = async (data) => {
    try {
      await axios.post(`/api/system/admin/edit/resultplan/resultplan/habukhan/secure/boss/asd/asd/changes/${AccessToken}/secure`, data)
      navigate(PATH_ADMIN.plan.newresult);
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }


  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(!isEdit ? onSubmit : onEdit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFSelect name="exam_name" label="Exam name" defaultValue="WAEC">
                <option value="" />
                {Habukhanexam?.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField name="pin" label="Result Checker Pin" multiline="true" rows="5" />
              <RHFTextField name="serial" label="Serial Number" multiline="true" rows="5" />
              <RHFSwitch
                name="plan_status"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      {values.plan_status ? 'SOLD' : 'AVAILABLE'}
                    </Typography>
                  </>
                }

                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />

            </Box>

            <Stack alignItems="flex-end" sx={{
              mt: 2, display: 'grid',
              columnGap: 1,
              rowGap: 2, gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
            }}>

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {!isEdit ? 'Create then Return ' : 'Save Changes then Return'}
              </LoadingButton>
              <LoadingButton type="button" onClick={handleSubmit(!isEdit ? onCreateReset : onEditReset)} variant="contained" color="warning" loading={isSubmitting}>
                {!isEdit ? ' Create then Reset' : 'Save Changes then Create New'}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
